<template>
  <div class="style-editing-panel" v-if="activeComponentID">
    <el-tabs stretch>
      <el-tab-pane label="内容" name="0">
        <div class="menu-wrapper">
          <div class="groud"></div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="样式" name="1">
        <div class="menu-wrapper">
          <background-change
            :background="background"
            @change="backgroundChange"
          />
          <slider
            :value="opacity"
            :opts="{ placeholder: '透明度', inputBox: true, hasRate: true }"
            @valueChange="opacityChange"
          />
          <a-divider />
          <div style="margin-bottom: 10px;">指示点</div>
          <div class="dot-color-selecter-wrap">
            <color-selector
              @change="activeColorChange"
              :color="activeDotColor"
              :showDesc="true"
              :label="'选中'"
            />
            <color-selector
              @change="defaultColorChange"
              :color="defaultDotColor"
              :showDesc="true"
              :label="'默认'"
            />
          </div>
          <slider
            :value="defaultDotOpacity"
            :opts="{ placeholder: '透明度', inputBox: true, hasRate: true }"
            @valueChange="defaultDotOpacityChange"
          />
          <a-divider />
          <margin-edit @valueChange="marginChange" />
        </div>
      </el-tab-pane>
      <el-tab-pane label="动画" name="2"></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import colorSelector from "@/components/StyleMenuComponents/colorSelector.vue";
import slider from "@/components/StyleMenuComponents/slider.vue";
import bgChange from "@/components/StyleMenuComponents/backgroundChange.vue";
import marginEdit from "@/components/StyleMenuComponents/marginEdit.vue";
import mixinObject from "@/mixin.js";
const {
  componentStoreBaseOperate,
  backgroundUpdateOperate,
  opacityUpdateOperate,
  marginUpdateOperate
} = mixinObject;
export default {
  name: "carousel",
  mixins: [
    componentStoreBaseOperate,
    backgroundUpdateOperate,
    opacityUpdateOperate,
    marginUpdateOperate
  ],
  components: {
    "color-selector": colorSelector,
    "margin-edit": marginEdit,
    "background-change": bgChange,
    slider: slider
  },
  data() {
    return {};
  },
  methods: {
    activeColorChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          data: {
            dotStyle: {
              active: {
                background: value
              }
            }
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    defaultColorChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          data: {
            dotStyle: {
              default: {
                background: value
              }
            }
          }
        }
      };
      this.updateComponentInfo(payload);
    },
    defaultDotOpacityChange(value) {
      let payload = {
        id: this.activeComponentID,
        value: {
          data: {
            dotStyle: {
              default: {
                opacity: value / 100
              }
            }
          }
        }
      };
      this.updateComponentInfo(payload);
    }
  },
  computed: {
    activeDotColor() {
      return this.activatedComponent.componentInfo.data.dotStyle.active
        .background;
    },
    defaultDotColor() {
      return this.activatedComponent.componentInfo.data.dotStyle.default
        .background;
    },
    defaultDotOpacity() {
      return (
        this.activatedComponent.componentInfo.data.dotStyle.default.opacity *
        100
      );
    }
  }
};
</script>
<style lang="stylus" scoped>
.dot-color-selecter-wrap
  display flex
  .color-select-wrapper:last-child
    margin-left 16px
</style>
