import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState, mapGetters } = createNamespacedHelpers(
  "componentStore"
);
export default {
  componentStoreBaseOperate: {
    computed: {
      ...mapState(["activeComponentID"]),
      ...mapGetters(["activatedComponent"])
    },
    methods: {
      ...mapActions(["updateComponentInfo"])
    }
  },
  marginUpdateOperate: {
    methods: {
      marginChange(message) {
        let payload = {
          id: this.activeComponentID,
          value: {
            style: {
              [message.key]: `${message.value}px`
            }
          }
        };
        this.updateComponentInfo(payload);
      }
    }
  },
  opacityUpdateOperate: {
    methods: {
      opacityChange(value) {
        let payload = {
          id: this.activeComponentID,
          value: {
            style: {
              opacity: value / 100
            }
          }
        };
        this.updateComponentInfo(payload);
      }
    },
    computed: {
      opacity() {
        return this.activatedComponent.componentInfo.style.opacity * 100;
      }
    }
  },
  backgroundUpdateOperate: {
    methods: {
      backgroundChange(val) {
        let payload = {
          id: this.activeComponentID,
          value: {
            style: {
              background: val
            }
          }
        };
        this.updateComponentInfo(payload);
      }
    },
    computed: {
      background() {
        return this.activatedComponent.componentInfo.style["background"];
      }
    }
  }
};
